<template>
  <header :class="{ active: isActive }" class="header">
    <div class="container">
      <div class="header__menu">
        <div class="header__logo">
          Movie App
        </div>
        <div class="header__genres">
          <select @click="changeGenre">
            <option value="all">
              Все
            </option>
            <option value="fantastic">
              Фантастика
            </option>
            <option value="drama">
              Драма
            </option>
          </select>
        </div>
        <div class="header__search">
          <form data-input="1" @submit="getMovie">
            <input @focus="addInputSearch" v-model="search" class="header__search" placeholder="Введите фильм..." type="search">
            <input class="header__submit" type="submit">
            <div @click="closeSearch" ref="searchInput" class="header__input-wrapper">
              <div ref="inputNames" class="header__example" :key="nameMovie" v-for="nameMovie in getNameMovie">
                <p  class="header__input" @click="sendToInput" v-if="search.length >= 1">{{ nameMovie.nameRu }}</p>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </header>
  <section class="content">
    <div class="container">
      <div class="empty" ref="empty">Ничего не найдено</div>
      <div ref="wrapper" class="content__wrapper">
        <div ref="block" @click="$router.push(`/movie/${item.filmId}`)" :key="item" v-for="item in data.films" class="content__block">
          <img class="content__img" :src="item.posterUrl" alt="movie">
          <p class="content__rate">{{ item.rating }}</p>
          <p ref="name" class="content__name">{{ item.nameRu }}</p>
          <div class="content__genre-wrapper">
            <p class="content__genre" :key="genre" v-for="genre in item.genres">{{ genre.genre }}</p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ['data'],
  data() {
    return {
      search: '',
      scrollToY: 0,
      scrollLast: 0,
      isActive: false,
      beginHide: 400,
    }
  },
  mounted() {
    window.addEventListener('scroll', () => {
      this.scrollPosition();
      if (this.scrollPosition() > this.scrollLast && this.scrollPosition() > this.beginHide) {
        this.isActive = true;
      } else if (this.scrollPosition() < this.scrollLast) {
        this.isActive = false;
      }
      this.scrollLast = this.scrollPosition();
    })
  },
  computed: {
    getNameMovie() {
      return this.data.films.filter((el) => {
        return el.nameRu.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  methods: {
    sendToInput(e) {
      this.search = e.target.textContent;
      if (e.target) {
        e.target.style.display = 'none';
      }
      this.getMovie(e);
    },
    getMovie(e) {
      e.preventDefault();
      for (let i = 0; i < this.data.films.length; i++) {
        if (this.$refs.name[i].textContent.toLocaleLowerCase().includes(this.search.toLocaleLowerCase())) {
          this.$refs.block[i].style.display = 'flex';
        } else if (this.$refs.name[i].textContent.toLocaleLowerCase().includes(this.search.toLocaleLowerCase()) === false) {
          this.$refs.block[i].style.display = 'none';
        }
        this.$refs.empty.style.display = 'none';
      }
      if (this.$refs.wrapper.offsetHeight === 0) {
        this.$refs.empty.style.display = 'block';
      }
      this.$refs.searchInput.style.display = 'none';
    },
    addInputSearch() {
      this.$refs.searchInput.style.display = 'block';
    },
    changeGenre(e) {
      if (e.target.value === 'fantastic') {
        console.log('click1')
        this.$router.push('/fantastic')
      } else if (e.target.value === 'drama') {
        console.log('click2')
      }
    },
    scrollPosition() {
      return this.scrollToY = window.scrollY;
    }
  }
}
</script>

<style lang="sass" scoped>
.header.active
  transform: translateY(-100px)

.header
  padding: 25px 0
  color: white
  background-color: #483f3f
  margin-bottom: 50px
  position: fixed
  z-index: 2
  top: 0
  left: 0
  width: 100%
  transform: translateY(0)
  transition: 0.4s ease all

  &__menu
    display: flex
    justify-content: space-between
    align-items: center

  &__search
    outline: none
    width: 200px
    height: 35px
    padding-left: 10px
    border: none

  &__submit
    display: none

  &__input
    cursor: pointer
    color: black
    background-color: white
    width: 200px
    padding: 10px
    font-size: 14px
    transition: 0.4s ease all
    position: relative
    z-index: 1

  &__input:focus.header__input-wrapper
    display: block

  &__input:hover
    background-color: #b4b4b7

  &__input-wrapper
    position: fixed


.content
  margin-top: 150px

  .empty
    display: none
    color: white
    text-align: center

  .content__wrapper
    display: flex
    justify-content: flex-start
    flex-wrap: wrap
    gap: 30px

  .content__block
    width: 30%
    display: flex
    align-items: center
    flex-direction: column
    gap: 10px
    position: relative
    cursor: pointer

  .content__img
    width: 100%

  .content__rate
    background-color: #49a928
    padding: 6px 10px 6px 10px
    border-radius: 10px
    position: absolute
    top: 10px
    left: 0
    color: white

  .content__name
    color: white
    text-align: center
    font-size: 25px

  .content__genre-wrapper
    display: flex
    justify-content: center
    gap: 10px
    flex-wrap: wrap

  .content__genre
    text-align: center
    color: lightyellow
    font-size: 16px
</style>