<template>
  <div v-if="filmCurrent && filmTrailer" class="film">
    <div class="film__wrapper">
      <div class="film__trailer">
      </div>
      <div class="film__img">
        <img class="film__img-inner" :src="filmCurrent.posterUrl" alt="img film">
      </div>
      <div class="film__name">
        {{ filmCurrent.nameRu }}
      </div>
      <div class="film__trailer">
        <a class="film__link"  target="_blank" :href="filmTrailer.items[1].url">Посмотреть трейлер</a>
      </div>
      <div class="film__back">
        <router-link to="/">Вернуться на главную</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      route: this.$route.params.id,
      filmCurrent: '',
      filmTrailer: '',
    }
  },
  created() {
    fetch(`https://kinopoiskapiunofficial.tech/api/v2.2/films/${this.route}`, {
      method: 'GET',
      headers: {
        'X-API-KEY': '3e6a29d8-635e-4047-964c-8b4835a492df',
        'Content-Type': 'application/json',
      },
    })
        .then(res1 => res1.json())
        .then(json1 => {
          this.filmCurrent = json1;
        })
        .catch(err1 => console.log(err1))

    fetch(`https://kinopoiskapiunofficial.tech/api/v2.2/films/${this.route}/videos`, {
      method: 'GET',
      headers: {
        'X-API-KEY': '3e6a29d8-635e-4047-964c-8b4835a492df',
        'Content-Type': 'application/json',
      },
    })
        .then(res2 => res2.json())
        .then(json2 => {
          this.filmTrailer = json2;
        })
        .catch(err2 => console.log(err2))
  },
}
</script>

<style lang="sass" scoped>
.film
  color: white
  position: absolute
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)

  &__wrapper
    display: flex
    flex-direction: column
    align-items: center

  &__name
    font-size: 30px

  &__img-inner
    max-width: 350px
    height: auto

  &__trailer
    margin: 20px 0 20px 0

  &__link
    color: aqua

</style>