import {createRouter, createWebHistory} from "vue-router";
import MainContent from "@/components/MainContent";
import FantasticGenre from "@/components/FantasticGenre";
import ContentIInner from "@/components/ContentIInner";

const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/',
            component: MainContent,
            name: 'mainContent'
        },
        {
            path: '/fantastic',
            component: FantasticGenre,
            name: 'fantasticGenre'
        },
        {
            path: '/movie/:id',
            component: ContentIInner,
        }
    ]
})

export default router;